import Image from "next/image";

const Hero = () => {
  return (
    <div className="relative w-full h-screen overflow-hidden">
      {/* Imagem de fundo */}
      <Image
        src="/assets/bg-image-hero.png"
        alt="Hero background"
        fill
        className="h-full w-full"
      />

      {/* Conteúdo centralizado sobre a imagem */}
      <div className="absolute inset-0 flex flex-col gap-24 items-center justify-center z-10">
        <Image
          src="/assets/image-pretorian-hero.png"
          alt="Logo Central"
          width={450}
          height={450}
          className="object-contain"
        />
        <p style={{fontFamily: 'AtypDisplay'}} className="text-white font-bold text-5xl " >Serviços Contábeis <br /> Especializados para <br />o Mercado Financeiro</p>
        <button style={{fontFamily: 'AtypDisplay'}} className="cursor-pointer border border-[#bfae67] rounded-3xl px-8 py-2 text-2xl text-white">
          Agendar Reunião
        </button>
      </div>
    </div>
  );
};

export default Hero;