import Image from "next/image";

const SectionOne = () => {
  return (
    <div className="relative w-full h-screen overflow-hidden">
      <div className="relative w-full h-full flex flex-col sm:flex-row z-0">
        {/* Lado esquerdo: imagem de fundo (esconde no mobile) */}
        <div className="w-1/2 h-full relative hidden sm:block">
          <Image
            src="/assets/bg-image-hero.png"
            alt="Fundo esquerdo"
            fill
            className="object-cover"
          />
        </div>

        {/* Lado direito: imagem + texto (100% no mobile, 50% no desktop) */}
        <div className="w-full sm:w-1/2 h-full relative">
          <Image
            src="/assets/bg-image-section1.png"
            alt="Logo Central"
            fill
            className="object-cover"
          />

          <div className="absolute inset-0 flex flex-col items-center justify-center text-white text-center px-6 gap-6 z-10">
            {/* <div className="" >
                <Image
                    src={}
                
                />
                <p>Pretorian</p>
                <p className="text-[#e6c887]">asset</p>
            </div> */}
            <p className="text-lg text-left sm:text-center  line sm:text-xl md:text-2xl lg:text-3xl leading-relaxed max-w-[90%]">
              Unimos a força de uma empresa que já impactou mais de <strong>R$ 2 bilhões</strong> em operações no Brasil com a inteligência operacional
              de quem vive e entende o que é <strong>escalar negócios</strong>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SectionOne;
